.horizontalMenu>.horizontalMenu-list>li {
    >ul.sub-menu {
        opacity: 0;
        visibility: hidden;
        -webkit-transform-origin: center center;
        -moz-transform-origin: center center;
        -ms-transform-origin: center center;
        -o-transform-origin: center center;
        transform-origin: center center;
        -webkit-transform: translateY(20px);
        -moz-transform: translateY(20px);
        -ms-transform: translateY(20px);
        -o-transform: translateY(20px);
        transform: translateY(20px);
        -o-transition: -o-transform 0.3s, opacity 0.3s;
        -ms-transition: -ms-transform 0.3s, opacity 0.3s;
        -moz-transition: -moz-transform 0.3s, opacity 0.3s;
        -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
    }
    &:hover {
        >ul.sub-menu,
        >.horizontal-megamenu {
            opacity: 1;
            visibility: visible;
            -webkit-transform: rotateX(0deg);
            -moz-transform: rotateX(0deg);
            -ms-transform: rotateX(0deg);
            -o-transform: rotateX(0deg);
            transform: rotateX(0deg);
        }
    }
    > {
        ul.sub-menu>li {
            >ul.sub-menu {
                opacity: 0;
                visibility: hidden;
                -webkit-transform-origin: center center;
                -moz-transform-origin: center center;
                -ms-transform-origin: center center;
                -o-transform-origin: center center;
                transform-origin: center center;
                -webkit-transform: translateY(20px);
                -moz-transform: translateY(20px);
                -ms-transform: translateY(20px);
                -o-transform: translateY(20px);
                transform: translateY(20px);
                -o-transition: -o-transform 0.3s, opacity 0.3s;
                -ms-transition: -ms-transform 0.3s, opacity 0.3s;
                -moz-transition: -moz-transform 0.3s, opacity 0.3s;
                -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
            }
            &:hover>ul.sub-menu {
                opacity: 1;
                visibility: visible;
                -webkit-transform: rotateX(0deg);
                -moz-transform: rotateX(0deg);
                -ms-transform: rotateX(0deg);
                -o-transform: rotateX(0deg);
                transform: rotateX(0deg);
            }
            >ul.sub-menu>li {
                >ul.sub-menu {
                    opacity: 0;
                    visibility: hidden;
                    -webkit-transform-origin: center center;
                    -moz-transform-origin: center center;
                    -ms-transform-origin: center center;
                    -o-transform-origin: center center;
                    transform-origin: center center;
                    -webkit-transform: translateY(20px);
                    -moz-transform: translateY(20px);
                    -ms-transform: translateY(20px);
                    -o-transform: translateY(20px);
                    transform: translateY(20px);
                    -o-transition: -o-transform 0.3s, opacity 0.3s;
                    -ms-transition: -ms-transform 0.3s, opacity 0.3s;
                    -moz-transition: -moz-transform 0.3s, opacity 0.3s;
                    -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
                }
                &:hover>ul.sub-menu {
                    opacity: 1;
                    visibility: visible;
                    -webkit-transform: rotateX(0deg);
                    -moz-transform: rotateX(0deg);
                    -ms-transform: rotateX(0deg);
                    -o-transform: rotateX(0deg);
                    transform: rotateX(0deg);
                }
            }
        }
        .horizontal-megamenu {
            opacity: 0;
            visibility: hidden;
            -webkit-transform-origin: center center;
            -moz-transform-origin: center center;
            -ms-transform-origin: center center;
            -o-transform-origin: center center;
            transform-origin: center center;
            -webkit-transform: translateY(20px);
            -moz-transform: translateY(20px);
            -ms-transform: translateY(20px);
            -o-transform: translateY(20px);
            transform: translateY(20px);
            -o-transition: -o-transform 0.3s, opacity 0.3s;
            -ms-transition: -ms-transform 0.3s, opacity 0.3s;
            -moz-transition: -moz-transform 0.3s, opacity 0.3s;
            -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
        }
    }
}

.horizontalMenu {
    html,
    body,
    iframe,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 0;
        padding: 0;
        border: 0;
        font: inherit;
        vertical-align: baseline;
        font-size: 12px;
        line-height: 18px;
    }
    .cl {
        clear: both;
    }
    img {
        border: 0 none;
        max-width: 100%;
    }
    a:focus {
        outline: none;
    }
    &:before {
        content: "";
        display: table;
    }
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

object,
embed,
video {
    border: 0 none;
    max-width: 100%;
}


/* ================== Desktop Main Menu CSS ================== */

.horizontalMenucontainer {
    background-size: cover;
    overflow: hidden;
    background-attachment: fixed;
    background-position: 50% 0;
    background-repeat: no-repeat;
}

.horizontal-main {
    width: 100%;
    position: fixed;
    background: $white;
    border-bottom: 1px solid #eff0f6;
    border-top: 1px solid #eff0f6;
    z-index: 99;
    box-shadow: 0px 10px 10px 20px rgba(176, 184, 214, 0.09), 10px 10px 15px -5px #b0b8d6;
}

.horizontal-mainwrapper {
    margin: 0 auto;
    /*max-width: 1200px;*/
    width: 100%;
    z-index: 999;
}

.desktoplogo {
    padding: 1.5rem 0;
    margin: 0;
    float: left;
    img {
        width: 100%;
    }
}

.horizontalMenu {
    color: $white;
    font-size: 14px;
    padding: 0;
    display: block;
    >.horizontalMenu-list {
        text-align: right;
        margin: 0 auto 0 auto;
        width: 100%;
        display: block;
        padding: 0;
    }
}

.mega-menubg {
    border: 1px solid #eff0f6;
    background: $white;
    font-size: 15px;
    padding: 14px 5px;
    box-shadow: 0px 10px 10px 20px rgba(68, 84, 195, 0.05), 10px 10px 15px -5px rgba(68, 84, 195, 0.26);
}

.horizontalMenu>.horizontalMenu-list>li {
    text-align: center;
    display: block;
    padding: 0;
    margin: 0;
    float: right;
    color: $primary;
    /*padding: 1.5rem 0;*/
    >a {
        display: block;
        padding: 1rem 0.8rem 1rem;
        text-decoration: none;
        position: relative;
        margin: 0;
        border-right: 1px solid rgb(239, 240, 246);
    }
    &:first-child>a {
        margin-left: 0;
    }
    &:last-child>a {
        border-right: 1px solid rgb(239, 240, 246);
    }
    a.menuhomeicon {
        padding-left: 25px;
        padding-right: 25px;
    }
    >a {
        i {
            display: inline-block;
            font-size: 16px;
            line-height: inherit;
            margin-right: 10px;
            vertical-align: top;
        }
        .fa.fa-home {
            font-size: 15px;
        }
        .arrow:after {
            border-left: 4px solid rgba(0, 0, 0, 0);
            border-right: 4px solid rgba(0, 0, 0, 0);
            border-top: 4px solid;
            content: "";
            float: right;
            right: 11px;
            height: 0;
            margin: 0 0 0 14px;
            position: absolute;
            text-align: right;
            top: 33px;
            width: 0;
        }
    }
    &.rightmenu {
        float: right;
        a {
            padding: 0px 30px 0px 20px;
            border-right: none;
            .i {
                font-size: 15px;
            }
        }
        float: right;
        &:before {
            content: "";
            display: table;
        }
    }
    &.rightmenu {
        &:after {
            clear: both;
        }
        >.topmenusearch {
            float: right;
            width: 210px;
            height: 39px;
            position: relative;
            margin: 16px 0px 0px 0px;
            .searchicon {
                -webkit-transition: all 0.7s ease 0s;
                -moz-transition: all 0.7s ease 0s;
                -o-transition: all 0.7s ease 0s;
                transition: all 0.7s ease 0s;
            }
            input {
                width: 100%;
                position: relative;
                float: right;
                top: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                border: 0;
                padding: 0;
                margin: 0;
                text-indent: 15px;
                height: 39px;
                z-index: 2;
                outline: none;
                color: #333;
                background-color: #efefef;
                -webkit-transition: all 0.7s ease 0s;
                -moz-transition: all 0.7s ease 0s;
                -o-transition: all 0.7s ease 0s;
                transition: all 0.7s ease 0s;
                font-size: 12px;
                &::placeholder {
                    color: #b3bdca;
                }
                &:focus {
                    color: #333;
                    width: 230px;
                    ~ {
                        .btnstyle .fa {
                            color: #6b6f80;
                            opacity: 1;
                        }
                        .searchicon {
                            opacity: 1;
                            z-index: 3;
                            color: #FFFFFF;
                        }
                    }
                }
            }
            .btnstyle {
                top: 0px;
                position: absolute;
                right: 0;
                bottom: 0;
                width: 40px;
                line-height: 30px;
                z-index: 1;
                cursor: pointer;
                opacity: 0.3;
                color: #333;
                z-index: 1000;
                background-color: transparent;
                border: solid 0px;
                -webkit-transition: all 0.7s ease 0s;
                -moz-transition: all 0.7s ease 0s;
                -o-transition: all 0.7s ease 0s;
                transition: all 0.7s ease 0s;
                .fa {
                    line-height: 37px;
                    margin: 0;
                    padding: 0;
                    text-align: center;
                }
                &:hover .fa {
                    opacity: 1;
                }
            }
        }
        zoom: 1;
    }
    > {
        ul.sub-menu {
            position: absolute;
            top: 56px;
            z-index: 1;
            margin: 0px;
            padding: 5px 0;
            min-width: 190px;
            background-color: $white;
            border: 1px solid #eff0f6;
            box-shadow: 0px 10px 10px 20px rgba(68, 84, 195, 0.05), 10px 10px 15px -5px rgba(68, 84, 195, 0.26);
            >li {
                position: relative;
                margin: 0px;
                padding: 0px;
                display: block;
                > {
                    a {
                        background-image: none;
                        color: $color;
                        border-right: 0 none;
                        text-align: right;
                        display: block;
                        line-height: 22px;
                        padding: 8px 12px;
                        text-transform: none;
                        font-size: 13px;
                        letter-spacing: normal;
                        border-right: 0px solid;
                        &:hover {
                            text-decoration: none;
                            color: $primary;
                        }
                        >.i {
                            margin-right: 9px;
                        }
                    }
                    ul.sub-menu {
                        min-width: 190px;
                        position: absolute;
                        right: 100%;
                        top: 0;
                        margin: 0;
                        padding: 2px 0;
                        list-style: none;
                        background-color: $white;
                        border: 1px solid #eff0f6;
                        box-shadow: 0px 10px 10px 20px rgba(68, 84, 195, 0.05), 10px 10px 15px -5px rgba(68, 84, 195, 0.26);
                        >li {
                            position: relative;
                            margin: 0px;
                            padding: 1px;
                            display: block;
                            > {
                                a {
                                    background-image: none;
                                    color: #6b6f80;
                                    border-left: 0 none;
                                    text-align: right;
                                    display: block;
                                    line-height: 22px;
                                    padding: 8px 12px;
                                    text-transform: none;
                                    font-size: 13px;
                                    letter-spacing: normal;
                                    border-left: 0px solid;
                                    &:hover {
                                        text-decoration: none;
                                    }
                                    >.i {
                                        margin-right: 9px;
                                    }
                                }
                                ul.sub-menu {
                                    min-width: 220px;
                                    position: absolute;
                                    left: 100%;
                                    top: 0;
                                    margin: 0px;
                                    list-style: none;
                                    padding: 0px;
                                    background-color: $white;
                                    border: 1px solid #eff0f6;
                                    >li {
                                        position: relative;
                                        margin: 0px;
                                        padding: 0px;
                                        display: block;
                                        >a {
                                            background-image: none;
                                            color: #6b6f80;
                                            border-right: 0 none;
                                            text-align: left;
                                            display: block;
                                            line-height: 22px;
                                            padding: 8px 12px;
                                            text-transform: none;
                                            font-size: 13px;
                                            letter-spacing: normal;
                                            border-right: 0px solid;
                                            &:hover {
                                                text-decoration: none;
                                            }
                                            >.i {
                                                margin-right: 9px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        a {
            &.active {
                color: $primary;
                text-decoration: none;
                background: #f4f5fc;
                .hor-icon {
                    color: $primary;
                    fill: rgba(68, 84, 195, 0.3);
                }
            }
            &:hover {
                color: $primary;
                text-decoration: none;
                .hor-icon {
                    color: $primary;
                    fill: rgba(68, 84, 195, 0.3);
                }
            }
        }
        ul.sub-menu {
            .sub-menu-sub:after {
                content: "\e92e";
                font-family: 'feather';
                font-size: 13px;
                position: absolute;
                top: 11px;
                left: 14px;
                color: #70768e;
            }
            >li> {
                a:hover,
                ul.sub-menu>li>a:hover {
                    text-decoration: none;
                    color: $primary;
                }
            }
        }
        .horizontal-megamenu {
            .link-list li a:hover {
                text-decoration: none;
                color: $primary;
            }
            width: 100%;
            left: 0px;
            position: absolute;
            top: 56px;
            z-index: 1;
            margin: 0px;
            text-align: left;
            font-size: 15px;
            .megamenu-content {
                border: 1px solid #eff0f6;
                background-color: $white;
                padding: 14px 20px;
            }
            .title {
                padding: 5px 5px 5px 0px;
                font-size: 14px;
                color: #495057;
                margin: 0px 0px 4px 0px;
                text-align: left;
                font-weight: 600;
            }
            .wsmwnutxt {
                width: 100%;
                font-size: 14px;
                text-align: justify;
                line-height: 22px;
                color: #424242;
                margin-top: 10px;
            }
            .link-list li {
                display: block;
                text-align: center;
                white-space: nowrap;
                text-align: right;
                padding: 5px 25px 5px 10px;
                a {
                    line-height: 18px;
                    border-left: none;
                    text-align: right;
                    padding: 6px 0px;
                    /*background: rgb(255,255,255,0.04);*/
                    background-image: none;
                    color: $color;
                    border-left: 0 none;
                    display: block;
                    font-size: 13px;
                }
            }
            li {
                .i {
                    margin-left: 5px;
                    text-align: center;
                    width: 18px;
                }
                a:hover {
                    background: transparent;
                    text-decoration: none;
                }
            }
            .link-list li .i {
                font-size: 12px;
            }
            li .i {
                margin-right: 5px;
                text-align: center;
                width: 18px;
            }
            .mrgtop {
                margin-top: 15px;
            }
            .show-grid div {
                padding-bottom: 10px;
                padding-top: 10px;
                background-color: #dbdbdb;
                border: 1px solid #e7e7e7;
                color: #6a6a6a;
                margin: 2px 0px;
                font-size: 13px;
            }
            &.halfmenu {
                width: 30%;
                right: auto;
                left: auto;
            }
            &.halfdiv {
                width: 20%;
                right: auto;
                left: auto;
            }
            .menu_form {
                width: 100%;
                display: block;
                input[type="text"] {
                    width: 100%;
                    border: 1px solid #e2e2e2;
                    color: #6b6f80;
                    font-size: 13px;
                    padding: 8px 5px;
                    margin-bottom: 8px;
                }
                textarea {
                    width: 100%;
                    border: 1px solid #e2e2e2;
                    color: #6b6f80;
                    font-size: 13px;
                    padding: 8px 5px;
                    margin-bottom: 8px;
                    min-height: 122px;
                }
                input {
                    &[type="submit"] {
                        width: 25%;
                        display: block;
                        height: 32px;
                        float: right;
                        border: none;
                        margin-right: 15px;
                        cursor: pointer;
                        background-color: #e1e1e1;
                        -webkit-border-radius: 2px;
                        -moz-border-radius: 2px;
                        border-radius: 2px;
                    }
                    &[type="button"] {
                        width: 25%;
                        display: block;
                        height: 32px;
                        float: right;
                        border: none;
                        cursor: pointer;
                        background-color: #e1e1e1;
                        -webkit-border-radius: 2px;
                        -moz-border-radius: 2px;
                        border-radius: 2px;
                    }
                }
            }
            .carousel-inner .item img {
                width: 100%;
            }
            .carousel-control-next,
            .carousel-control-prev {
                opacity: 0.8;
            }
            .carousel-caption {
                bottom: 0px;
                background-color: $black-7;
                font-size: 13px;
                height: 31px;
                left: 0;
                padding: 7px 0;
                right: 0;
                width: 100%;
            }
            .typography-text {
                padding: 0px 0px;
                font-size: 14px;
                ul {
                    padding: 0px 0px;
                    margin: 0px;
                }
                p {
                    text-align: justify;
                    line-height: 24px;
                    color: #656565;
                }
                ul li {
                    display: block;
                    padding: 2px 0px;
                    line-height: 22px;
                    a {
                        color: #656565;
                    }
                }
            }
        }
    }
}


