                                }
                                &:hover>a {
                                    color: #6b6f80;
                                }
                                >ul.sub-menu {
                                    width: 100%;
                                    position: static;
                                    left: 100%;
                                    top: 0;
                                    display: none;
                                    margin: 0px;
                                    padding: 0px;
                                    border: solid 0px;
                                    transform: none;
                                    opacity: 1;
                                    visibility: visible;
                                    -webkit-transform: none;
                                    -moz-transform: none;
                                    -ms-transform: none;
                                    -o-transform: none;
                                    transform: none;
                                    -webkit-transition: inherit;
                                    -moz-transition: inherit;
                                    transition: inherit;
                                    -webkit-transform-style: flat;
                                    >li {
                                        margin: 0px 0px 0px 0px;
                                        >a {
                                            line-height: 20px;
                                            font-size: 13px;
                                            padding: 10px 0px 10px 34px;
                                            color: #6b6f80;
                                        }
                                        span+a {
                                            padding-right: 30px;
                                        }
                                        >a {
                                            >.fa {
                                                display: none;
                                            }
                                            &:hover {
                                                background-color: #e7e7e7;
                                                color: #6b6f80;
                                                text-decoration: underline;
                                            }
                                            &.active {
                                                color: #6b6f80;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .horizontal-megamenu {
                        color: #6b6f80;
                        display: none;
                        position: relative;
                        top: 0px;
                        padding: 10px 0px;
                        border: solid 0px;
                        transform: none;
                        opacity: 1;
                        visibility: visible;
                        -webkit-transform: none;
                        -moz-transform: none;
                        -ms-transform: none;
                        -o-transform: none;
                        transform: none;
                        -webkit-transition: inherit;
                        -moz-transition: inherit;
                        transition: inherit;
                        border-bottom: 1px solid rgba(255, 255, 255, 0.13);
                        -webkit-transform-style: flat;
                        &.halfmenu {
                            width: 100%;
                            margin: 0px;
                            padding: 5px 0px 10px 0px;
                        }
                        .title {
                            color: #6b6f80;
                            font-size: 15px;
                            padding: 10px 8px 10px 0px;
                        }
                        >ul {
                            width: 100%;
                            margin: 0px;
                            padding: 0px;
                            font-size: 13px;
                            >li>a {
                                padding: 9px 14px;
                                line-height: normal;
                                font-size: 13px;
                                background-color: #e7e7e7;
                                color: #6b6f80;
                                &:hover {
                                    background-color: #6b6f80;
                                }
                            }
                        }
                        ul li.title {
                            line-height: 26px;
                            color: #6b6f80;
                            margin: 0px;
                            font-size: 15px;
                            padding: 7px 15px;
                            background-color: transparent;
                        }
                        &.halfdiv {
                            width: 100%;
                        }
                        .menu_form {
                            padding: 5px 0px 62px 0px;
                        }
                        .show-grid div {
                            margin: 0px;
                        }
                        .menu_form {
                            input {
                                &[type="button"],
                                &[type="submit"] {
                                    width: 46%;
                                }
                            }
                            textarea {
                                min-height: 100px;
                            }
                        }
                    }
                }
            }
        }
    }
    /* ================== Mobile Slide Down Links CSS ================== */
    /* ================== Mobile Mega Menus CSS  ================== */
    /* ================== Mobile Header CSS ================== */
    .horizontal-header {
        width: 100%;
        display: block;
        position: relative;
        top: 0;
        left: 0;
        z-index: 1;
        height: 54px;
        background-color: #eaecf0;
        text-align: center;
        -webkit-transition: all 0.25s ease-in-out;
        -moz-transition: all 0.25s ease-in-out;
        -o-transition: all 0.25s ease-in-out;
        -ms-transition: all 0.25s ease-in-out;
        transition: all 0.25s ease-in-out;
        box-shadow: 0 0 1px $black-3;
    }
    .active .horizontal-header {
        -webkit-transition: all 0.25s ease-in-out;
        -moz-transition: all 0.25s ease-in-out;
        -o-transition: all 0.25s ease-in-out;
        -ms-transition: all 0.25s ease-in-out;
        transition: all 0.25s ease-in-out;
    }
    .active .header.top-header {
        box-shadow: none;
    }
    .horizontal-header .smllogo {
        display: inline-block;
        margin-top: 15px;
    }
    /* Mobile Search Bar*/
    .horizontalMenu>.horizontalMenu-list>li.rightmenu>.topmenusearch {
        width: 86%;
        margin: 7% 7%;
        padding: 0px;
        input {
            border-radius: 50px;
            &:focus {
                width: 100%;
            }
        }
    }
    .callusbtn {
        color: #6b6f80;
        font-size: 21px;
        position: absolute;
        right: 5px;
        top: 0px;
        transition: all 0.4s ease-in-out 0s;
        z-index: 102;
        padding: 11px 14px;
        .fa {
            vertical-align: top;
            margin-top: 4px;
        }
        &:hover .fa {
            color: #6b6f80;
        }
    }
    /* Mobile Toggle Menu icon (X ICON) */
    .animated-arrow {
        position: absolute;
        top: -2px;
        z-index: 102;
        -webkit-transition: all 0.4s ease-in-out;
        -moz-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
        -ms-transition: all 0.4s ease-in-out;
        transition: all 0.4s ease-in-out;
        cursor: pointer;
        padding: 16px 0px 16px 35px;
        margin: 15px 0 0 15px;
        span {
            cursor: pointer;
            height: 2px;
            width: 17px;
            background: $primary;
            position: absolute;
            display: block;
            content: '';
            &:before,
            &:after {
                cursor: pointer;
                height: 2px;
                width: 17px;
                background: $primary;
                position: absolute;
                display: block;
                content: '';
            }
            &:before {
                top: -7px;
                width: 23px;
            }
            &:after {
                bottom: -7px;
                width: 17px;
            }
            transition: all 500ms ease-in-out;
            &:before,
            &:after {
                transition: all 500ms ease-in-out;
            }
        }
    }
    .active .animated-arrow {
        span {
            &:after {
                width: 23px;
                top: -3px;
            }
            background-color: transparent;
            &:before {
                top: -3px;
            }
        }
        &.active span:after {
            top: -3px;
        }
        span {
            &:before {
                transform: rotate(45deg);
                -moz-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                -o-transform: rotate(45deg);
                -webkit-transform: rotate(45deg);
                bottom: 0px;
            }
            &:after {
                transform: rotate(-45deg);
                -moz-transform: rotate(-45deg);
                -ms-transform: rotate(-45deg);
                -o-transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
            }
        }
    }
    /* ================== Mobile Overlay/Drawer CSS ================== */
    .overlapblackbg {
        left: 0;
        width: calc(100% - 240px);
        height: 100vh;
        min-height: 100%;
        position: fixed;
        top: 0;
        opacity: 0;
        visibility: hidden;
        background-color: $black-4;
        cursor: pointer;
    }
    .active .horizontalMenu .overlapblackbg {
        // opacity: 1;
        visibility: visible;
        -webkit-transition: opacity 1.5s ease-in-out;
        -moz-transition: opacity 1.5s ease-in-out;
        -ms-transition: opacity 1.5s ease-in-out;
        -o-transition: opacity 1.5s ease-in-out;
    }
    .horizontalMenucontainer {
        -webkit-transition: all 0.25s ease-in-out;
        -moz-transition: all 0.25s ease-in-out;
        -o-transition: all 0.25s ease-in-out;
        -ms-transition: all 0.25s ease-in-out;
        transition: all 0.25s ease-in-out;
    }
    .active {
        .horizontalMenucontainer {
            margin-right: 240px;
            -webkit-transition: all 0.25s ease-in-out;
            -moz-transition: all 0.25s ease-in-out;
            -o-transition: all 0.25s ease-in-out;
            -ms-transition: all 0.25s ease-in-out;
            transition: all 0.25s ease-in-out;
        }
        .horizontalMenu {
            overflow-y: scroll;
            -webkit-overflow-scrolling: touch;
            visibility: visible;
            z-index: 1000;
            top: 0;
            height: 100% !important;
            >.horizontalMenu-list {
                -webkit-transition: all 0.25s ease-in-out;
                -moz-transition: all 0.25s ease-in-out;
                -o-transition: all 0.25s ease-in-out;
                -ms-transition: all 0.25s ease-in-out;
                transition: all 0.25s ease-in-out;
                margin-right: 0;
                border-right: 1px solid #eff0f6;
            }
        }
    }
    /* ================== Mobile Sub Menu Expander Arrows  ================== */
    .horizontalMenu>.horizontalMenu-list>li {
        >.horizontalMenu-click {
            height: 49px;
            position: absolute;
            top: 0;
            right: 0;
            display: block;
            cursor: pointer;
            width: 100%;
        }
        .horizontalMenu-click.ws-activearrow>i {
            transform: rotate(180deg);
        }
        > {
            .horizontalMenu-click>i {
                display: block;
                height: 23px;
                width: 25px;
                margin-top: 14px;
                margin-left: 10px;
                background-size: 25px;
                font-size: 21px;
                color: $black-2;
                float: left;
            }
            ul.sub-menu>li .horizontalMenu-click02 {
                height: 41px;
                position: absolute;
                top: 0;
                left: 0;
                display: block;
                cursor: pointer;
                width: 100%;
                >i {
                    display: block;
                    height: 23px;
                    width: 25px;
                    margin: 8px 6px 8px 6px;
                    background-size: 25px;
                    font-size: 21px;
                    color: $black-2;
                    float: left;
                    &.horizontalMenu-rotate {
                        -webkit-transform: rotate(180deg);
                        -moz-transform: rotate(180deg);
                        -ms-transform: rotate(180deg);
                        -o-transform: rotate(180deg);
                        transform: rotate(180deg);
                    }
                }
            }
        }
    }
    .mega-menubg {
        box-shadow: none !important;
        border: 0 !important;
        padding: 0 !important;
        background: $white-2 !important;
    }
    .horizontalMenu>.horizontalMenu-list>li {
        > {
            a {
                &.sub-icon:before,
                &:hover.sub-icon:before {
                    display: none;
                }
            }
            ul.sub-menu {
                .sub-menu-sub:after {
                    display: none;
                }
                >li>a:hover {
                    color: $primary;
                    text-decoration: none;
                }
                li:hover>a {
                    color: $primary !important;
                }
            }
        }
        &:hover>a {
            color: $primary !important;
            text-decoration: none;
        }
        >ul.sub-menu>li {
            &:hover>a {
                color: $primary;
                text-decoration: none;
            }
            >ul.sub-menu {
                box-shadow: none !important;
                border-radius: 0px !important;
                background: none !important;
                border: 0 !important;
            }
        }
    }
    .mega-menubg {
        &:before {
            display: none;
        }
        ul {
            padding: 0;
        }
    }
    .horizontalMenu>.horizontalMenu-list>li {
        > {
            ul.sub-menu {
                box-shadow: none;
                border-radius: 0 !important;
                &:before {
                    display: none;
                }
            }
            a.active>.fa {
                color: $primary !important;
            }
        }
        &:hover>a>.fa {
            color: $primary !important;
        }
        >a {
            >.horizontal-icon {
                display: none;
            }
            margin: 0;
        }
        &:last-child>a {
            border-right: 0px !important;
        }
    }
    /*End Media Query*/
}


