/***** CUSTOM STYLES *****/

.wizard-style-1>.steps>ul {
    a {
        padding: 0;
        height: 50px;
        &:hover,
        &:active {
            padding: 0;
            height: 50px;
        }
        .number,
        &:hover .number,
        &:active .number {
            width: 50px;
            height: 100%;
            border: 0;
            font-size: 18px;
            font-weight: bold;
            color: #737688;
            background-color: #f3f7fd;
            border-radius: 0;
        }
        .title,
        &:hover .title,
        &:active .title {
            margin-right: 20px;
            margin-left: 20px;
        }
    }
    .current a {
        .number,
        &:hover .number,
        &:active .number {
            background-color: $primary;
            color: $white;
        }
    }
    .done a {
        .number,
        &:hover .number,
        &:active .number {
            background-color: #643ab0;
            color: $white;
        }
    }
}

.wizard-style-2>.steps>ul {
    a {
        padding: 0;
        height: 50px;
        border-radius: 50px;
        &:hover,
        &:active {
            padding: 0;
            height: 50px;
            border-radius: 50px;
        }
        .number,
        &:hover .number,
        &:active .number {
            width: 50px;
            height: 100%;
            border: 2px solid #f3f7fd;
            font-size: 18px;
            font-weight: bold;
            color: #737688;
            background-color: $white;
        }
        .title,
        &:hover .title,
        &:active .title {
            margin-right: 20px;
        }
    }
    .current a {
        .number,
        &:hover .number,
        &:active .number {
            border-color: $primary;
            color: $primary;
        }
    }
    .done a {
        .number,
        &:hover .number,
        &:active .number {
            border-color: #8c3feb;
            color: #8c3feb;
        }
    }
}


/*--- WIZARD ELEMENTS ---*/

.parsley-required {
    font-size: 12px;
    color: #ff5c77;
    margin-top: 3px;
}