/*------ Custom Default -------*/
.loader {
  border: 2px solid #ffffff;
  border-top: 2px solid #000000;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
  display: inline-block;
  margin: 1rem 0;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.loader.animate {
  opacity: 1;
}

h2.animate {
  opacity: 1;
  transform: translateY(0);
}

