/*------ Pricing Styles ---------*/
.panel.price {
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  margin-bottom: 1.5rem;
  border: 1px solid #eff0f6;
  overflow: hidden;
  border-radius: 5px;
  overflow: hidden;
}
.panel.price > .panel-heading {
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
}
.panel.price h3 {
  margin-bottom: 0;
  padding: 20px 0;
}
.panel.price .list-group-item:last-child {
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
}
.panel.price .list-group-item:first-child {
  border-top-right-radius: 0px;
  border-top-left-radius: 0px;
}

.panel-heading {
  padding: 10px 15px;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  background: #fff;
}

.panel.price > .panel-heading {
  background: #4454c3;
  color: #fff;
}

.price .list-group-item {
  border-bottom: 1px solid rgba(250, 250, 250, 0.5);
}

.price .panel-footer {
  border-bottom: 0px;
  background-color: #fff;
}
.price.panel-color > .panel-body {
  background-color: #fff;
}

.panel-body {
  padding: 15px;
}
.panel-body .lead {
  font-size: 20px;
  margin-bottom: 0;
  padding: 10px 0;
}
.panel-body .lead strong {
  font-size: 40px;
  margin-bottom: 0;
}

.panel-footer {
  padding: 10px 15px;
  background-color: #f7f7f7;
  border-top: 1px solid #eff0f6;
  border-left: 1px solid #eff0f6;
  border-right: 1px solid #eff0f6;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
}

.panel.price .btn {
  box-shadow: 0 -1px 0px rgba(50, 50, 50, 0.2) inset;
  border: 0px;
}

/*end of pricing*/
@media (min-width: 992px) {
  .plan-card.active {
    margin-top: -25px;
    margin-left: -20px;
    width: 120%;
    height: 100%;
    z-index: 1;
    box-shadow: 0px 10px 10px 20px rgba(176, 184, 214, 0.09), 10px 10px 15px -5px rgba(176, 184, 214, 0.16);
    padding: 30px 0;
    font-size: 16px;
    padding-bottom: 20px;
  }
  .plan-card.active .plan-features p {
    line-height: 50px;
  }
  .plan-card.active .plan-card .plan-price span {
    font-size: 18px;
  }
  .plan-card.active .display-4 {
    font-size: 3.5rem;
  }

  .pricing-sctive .display-3 {
    font-size: 4rem;
  }

  .plan-card.active h6 {
    font-size: 16px;
  }

  .pricing-sctive {
    margin-top: -25px;
    margin-left: -20px;
    width: 120%;
    height: 100%;
    font-size: 16px;
    z-index: 1;
  }

  .panel.price.active {
    margin-top: -25px;
    margin-left: -20px;
    width: 120%;
    height: 100%;
    z-index: 8;
    box-shadow: 0px 10px 10px 20px rgba(176, 184, 214, 0.09), 10px 10px 15px -5px rgba(176, 184, 214, 0.08);
    position: relative;
    font-size: 16px;
    border-right: 1px !important;
  }

  .no-gutters .panel.price {
    border-right: 0;
  }
  .no-gutters .panel.price.active .panel-body .lead strong {
    font-size: 50px;
  }
  .no-gutters .panel.price.active .price-title {
    font-size: 26px;
  }
  .no-gutters .panel.price:last-child {
    border-right: 1px;
  }
}
