}

.btn-sm,
.btn-group-sm>.btn {
    font-size: 0.875rem;
    line-height: 1.33333333;
    border-radius: 3px;
}

.btn-block {
    display: block;
    width: 100%;
    +.btn-block {
        margin-top: 0.5rem;
    }
}

input {
    &[type="submit"].btn-block,
    &[type="reset"].btn-block,
    &[type="button"].btn-block {
        width: 100%;
    }
}

.fade {
    transition: opacity 0.15s linear;
    &:not(.show) {
        opacity: 0;
    }
}

@media screen and (prefers-reduced-motion: reduce) {
    .fade {
        transition: none;
    }
}

.collapse:not(.show) {
    display: none;
    background: $white;
}

.collapsing {
    position: relative;
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
}

@media screen and (prefers-reduced-motion: reduce) {
    .collapsing {
        transition: none;
    }
}


