body * {
    &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
        transition: .3s background;
    }
    &::-webkit-scrollbar-thumb,
    &:hover::-webkit-scrollbar-thumb {
        background: #c2cbdf;
    }
    &::-webkit-scrollbar-track {
        background: $white;
    }
}


/*------ Headers -------*/

.page-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    margin: 1.5rem 0 1.5rem;
    -ms-flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;
    border-radius: 7px;
    position: relative;
    min-height: 50px;
    .breadcrumb-icon {
        margin-top: 3px;
        margin-right: 4px;
    }
    .breadcrumb-item {
        .svg-icon {
            width: 20px;
        }
        &:first-child {
            margin-top: 0;
        }
        margin-top: 3px;
    }
}

.page-title {
    margin: 0;
    font-size: 25px;
    font-weight: 500;
    line-height: 2.5rem;
    position: relative;
}

.page-title-icon {
    color: #9aa0ac;
    font-size: 1.25rem;
}

.page-subtitle {
    font-size: 0.8125rem;
    color: #6e7687;
    margin-left: 2rem;
    position: relative;
    a {
        color: inherit;
    }
}

.page-options {
    margin-left: auto;
}

.page-description {
    margin: .25rem 0 0;
    color: #6e7687;
    a {
        color: inherit;
    }
}

.page-single,
.page-single-content {
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.content-heading {
    font-weight: 400;
    margin: 2rem 0 1.5rem;
    font-size: 1.25rem;
    line-height: 1.25;
    &:first-child {
        margin-top: 0;
    }
}

.aside {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 22rem;
    background: $white;
    border-left: 1px solid #eff0f6;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    z-index: 100;
    visibility: hidden;
    box-shadow: 0 5px 20px $black-1;
}

@media (min-width: 1600px) {
    body.aside-opened .aside {
        visibility: visible;
    }
}

.aside-body {
    padding: 1.5rem;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    overflow: auto;
}

.aside-footer {
    padding: 1rem 1.5rem;
    border-top: 1px solid #eff0f6;
}

.aside-header {
    padding: 1rem 1.5rem;
    border-bottom: 1px solid #eff0f6;
}

.header {
    padding-top: .75rem;
    padding-bottom: .75rem;
    background: $white;
    .dropdown-menu {
        margin-top: 0;
    }
    .mega-menu {
        width: 350px;
    }
}

.header-bg {
    background: #38a01e;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #38a01e, $primary);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #38a01e, $primary);
}

body.fixed-header .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1030;
}

@media print {
    .header {
        display: none;
