/*-------- Search ---------*/

.search-element {
    position: relative;
}

.header .form-inline {
    .form-control {
        border: 1px solid #D3DFEA;
        padding-right: 20px;
        width: 500px;
        padding-left: 40px;
        padding-top: 7px;
        background: #f4f5fc;
        &:focus {
            box-shadow: none;
        }
    }
    .btn {
        border-radius: 0;
        padding: 5px 15px 6px 15px;
        border: 2px solid transparent;
        box-shadow: none;
        border-bottom-left-radius: 3px;
        border-top-left-radius: 3px;
        background: transparent;
        color: $white;
        margin-right: -56px;
        font-size: 15px;
        position: absolute;
        left: 0;
        i {
            color: #818c9b;
        }
    }
}

@media (max-width: 767.98px) and (min-width: 576px) {
    .header .form-inline .search-element {
        position: absolute;
        top: 8px;
        left: 10px;
        right: 10px;
        z-index: 892;
        display: none;
    }
    body.search-show .header .form-inline .search-element {
        display: block;
    }
    .header .navsearch i {
        font-size: 20px;
        color: $white;
    }
    .search-element .form-control {
        float: left;
        width: 100% !important;
        height: 40px;
        margin: 0 auto;
        background: $white !important;
        color: $color;
        border: 1px solid #eff0f6;
    }
    .search-show .search-element {
        display: block;
        background: $primary;
        left: 0 !important;
        top: 0 !important;
        right: 0;
        width: 100%;
        padding: 18px 13px;
    }
    .header {
        .form-inline .form-control::-webkit-input-placeholder {
            color: #b3bdca !important;
        }
        .navsearch i {
            font-size: 20px;
            color: $color;
            padding-top: 5px;
        }
        .form-inline .btn {
            color: #46494a !important;
            padding: 8px 15px 6px 15px;
            position: unset !important;
        }
    }
    .animated-arrow.hor-toggle {
        text-align: center;
        height: 2.5rem;
        width: 2.5rem;
        font-size: 1.2rem;
        position: relative;
        border: 0px solid #eff0f6;
        border-radius: 3px;
        margin: 5px;
        top: 0;
        span {
            margin-left: 8px;
            margin-top: 2px;
            vertical-align: middle;
        }
    }
}

@media (max-width: 1024px) {
    .app.sidebar-gone.search-show .header.top-header {
        z-index: 1036;
    }
    body {
        &.search-show,
        &.sidebar-show {
            overflow: hidden;
        }
        &.search-show:before,
        &.sidebar-show:before {
            content: '';
            position: fixed;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background-color: $black-4;
            opacity: .6;
            z-index: 9;
        }
    }
}

@media (min-width: 575.98px) and (max-width: 1200px) {
    .header .form-inline .form-control {
        width: 200px;
    }
}

@media (max-width: 575.98px) {
    body.search-show .header .form-inline .search-element {
        display: block;
    }
    .header .form-inline .search-element .form-control {
        float: left;
        width: 100%;
        height: 40px;
        margin: 0 auto;
        background: $white;
        border-radius: 4px;
    }
    .search-show .search-element {
        display: block;
        background: $primary;
        left: 0;
        top: 0;
        right: 0;
        width: 100%;
        padding: 20px 13px;
    }
    .form-control.header-search {
        color: $black;
    }
    .header {
        .form-inline {
            .search-element {
                position: absolute;
                z-index: 892;
                display: none;
            }
            .btn {
                color: #46494a !important;
            }
            .form-control::-webkit-input-placeholder {
                color: #b3bdca !important;
            }
        }
        .navsearch i {
            font-size: 20px;
            color: $color;
            padding-top: 4px;
        }
    }
}

