.search {
    position: absolute;
    width: 320px;
    svg {
        transform: translateX(-126px);
        width: 180px;
        height: auto;
        stroke-width: 8px;
        stroke: $white;
        stroke-width: 1px;
        stroke-dashoffset: 0;
        stroke-dasharray: 64.6 206.305;
        transition: all 0.5s ease-in-out;
        margin-top: 5px;
    }
}

.input-search {
    position: absolute;
    width: calc(100% - 148px);
    height: 34px;
    top: 0;
    right: 20px;
    bottom: 0;
    left: 0;
    border: none;
    background-color: transparent;
    outline: none;
    padding: 20px;
    font-size: 16px;
    color: $white;
}

.search-label {
    position: absolute;
    display: block;
    width: 50px;
    height: 50px;
    top: 0;
    left: 50%;
    margin-left: -54px;
    z-index: 100;
    transition: 0.5s ease-in-out;
}

.isActive {
    .search-label {
        transform: translateX(246px);
    }
    svg,
    &.full svg {
        stroke-dashoffset: -65;
        stroke-dasharray: 141.305 65;
        transform: translateX(0);
    }
}

.full {
    .search-label {
        transform: translateX(246px);
    }
    svg {
        stroke-dashoffset: 0;
        stroke-dasharray: 64.6 206.305;
        transform: translateX(0);
    }
}

#drag-1,
#drag-2 {
    width: 25%;
    height: 100%;
    min-height: 6.5em;
    margin: 10%;
    background-color: #29e;
    color: white;
    border-radius: 0.75em;
    padding: 4%;
    user-select: none;
    -webkit-transform: translate(0px, 0px);
    transform: translate(0px, 0px);
}

.search-inline {
    width: 100%;
    left: 0;
    padding: 33px 0;
    top: 0;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    background-color: $white;
    z-index: 9;
    transition: all .5s ease-in-out;
    &.search-visible {
        opacity: 1;
        visibility: visible;
        animation: fadeInDown 0.2s ease-in-out;
    }
    button[type="submit"] {
        position: absolute;
        right: 52px;
        top: 0;
        background-color: transparent;
        border: 0px;
        top: 0;
        right: 80px;
        padding: 0px;
        cursor: pointer;
        width: 80px;
        height: 100%;
        background: $primary;
        color: $white;
    }
    .form-control {
        border: 0px;
        padding-left: 0;
        font-size: 20px;
        position: absolute;
        left: 1%;
        top: 0;
        height: 100%;
        width: 99%;
        outline: none;
        &:focus {
            box-shadow: none;
        }
    }
    .search-close {
        position: absolute;
        top: 0;
        right: 0;
        color: #616161;
        width: 80px;
        height: 100%;
        text-align: center;
        display: table;
        background: #efefef;
        text-decoration: none;
        i {
            display: table-cell;
            vertical-align: middle;
        }
    }
}

